<template>
		<div class="main clearfix" :style="{ 'margin-left': $store.state.app.isCollapse ? '84px' : '240px' }">
			<div class="main_container">
				<querys  :showSwitch = "false">
			      <div slot="queryTabs">

				    <div class="query_tab">
						<div class="query_label">专项类别</div>
						<div class="query_select">
							<el-select v-model="param.speciality" v-if="selectParamDict != undefined" @change="selectItem" placeholder="请选择" size="small" clearable>
								<el-option v-for="item in selectParamDict.get('speciality')" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
							</el-select>
						</div>
					</div>
                    
                    <div class="query_tab">
						<div class="query_label">批次名称</div>
						<div class="query_select">
							<el-input type="text" v-model="param.batchName" @change="selectItem" clearable size="small" min="1" placeholder="请输入"></el-input>
							
						</div>
					</div>

                    <div class="query_tab">
				      <div class="query_label">上报开始</div>
				      <div class="query_select">
				        <el-date-picker
				         style="width:150px!important"
				          v-model="param.reportStartDate"
				          format="yyyy-MM-dd"
				          @change="getTable"
				          value-format="yyyy-MM-dd"
				          type="date"
				          size = "small"
				          :picker-options="pickerOptionsStart"

				          placeholder="选择日期">
				        </el-date-picker>
				      </div>
				    </div>
				    <div class="query_tab">
				      <div class="query_label">上报结束</div>
				      <div class="query_select">
				        <el-date-picker
				         style="width:150px!important"
				          v-model="param.reportFinishDate"
				          format="yyyy-MM-dd"
				          @change="getTable"
				          value-format="yyyy-MM-dd"
				          type="date"
				          size = "small"
				          :picker-options="pickerOptions"

				          placeholder="选择日期">
				        </el-date-picker>
				      </div>
				    </div>

			      </div>
			      <div slot="queryBtns" v-if="$route.name == '评审批次管理'">
				    <el-button type="primary" size="small" @click="showEdit('新增批次',null)">新增</el-button>
					<el-button type="primary" size="small" @click="del()" >删除</el-button>
					<el-button type="primary" size="small" @click="publish()">发布</el-button>
			      </div>
			    </querys>


				
					<div>
						<el-table
							:data="tableShowData"
							v-loading="tableLoading"
							@selection-change="handleSelectionChange"
							:height="tableHeight"
							border
							:header-cell-style="{background:'#359691',color:'#fff'}"
							cell-class-name="column"
							:highlight-current-row="true"
							:row-key="getRowKeys"
							style="width:100%"
							ref="itemTable"
						>
							<el-table-column 
								type="selection" 
								:selectable="isSelect"
								
								width="45">
							</el-table-column>
							<el-table-column type="index" width="50" label="序号"></el-table-column>
							<el-table-column
						        prop="speciality"
						        label="专项类别"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="batch"
						        label="批次"
						        width="100">
						      </el-table-column>
						      <el-table-column
						        prop="batchName"
						        label="批次名称"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="batchCode"
						        label="批次编号"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="reportStartDate"
						        label="上报开始时间"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="reportFinishDate"
						        label="上报截止时间"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="batchStatus"
						        label="状态"
						        width="100">
						      </el-table-column>
						      <el-table-column
						        prop="auditStartDate"
						        label="计划评审开始时间"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="auditFinishDate"
						        label="计划评审开始时间"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="createUserNo"
						        label="创建人"
						        width="180">
						      </el-table-column>
						      <el-table-column
						        prop="createOrg"
						        label="创建单位"
						        width="180">
						      </el-table-column>

							<el-table-column label="操作" min-width="100" v-if="$route.name == '评审批次管理'">
								<template slot-scope="scope">
									<span class="blue" @click="showEdit('编辑批次',scope.row)" v-if="scope.row.batchStatus == '未发布'">编辑</span>
									<span class="gray" v-else >编辑</span>
								</template>
							</el-table-column>
						</el-table>
						<div class="page">
							<el-pagination
								background
								@size-change="handleSizeChange"
								@current-change="handleCurrentChange"
								:current-page="param.pageIndex"
								:page-sizes="[10,20,30,50]"
								:page-size="20"
								layout="sizes, total, prev, pager, next, jumper"
								:total="totalNum"
							></el-pagination>
						</div>
					</div>
					
				
			</div>
		<!---**************************编辑新增弹窗*************************************-->
		<el-dialog  :visible.sync="dialogFormVisible" 
		:close-on-click-modal = "false" custom-class="review_dialog" :title='dialogTitle'
		:modal-append-to-body='false' width="900px" @close="closeDialogForm" >
			
			<el-form ref="form" :model="form" label-width="120px" size = "small" :rules="rules">
            
	            <el-form-item label="专项类别" prop="speciality"
	            	:rules="[
				      { required: true, message: '请选择专项类别', trigger: 'change' }
				    ]">
				    <el-select v-model="form.speciality" placeholder="请选择专项类别">
				      <el-option v-for="item in selectParamDict.get('speciality')" :key="item.codeValue" :label="item.codeName" :value="item.codeValue"></el-option>
				    </el-select>
				</el-form-item>
				<el-form-item label="批次" prop="batch">
				    <el-input type="number" min="1" v-model.trim="form.batch" clearable size="small" placeholder="请输入"></el-input>
				    
				</el-form-item>
				<el-form-item label="批次名称" prop="batchName"
				:rules="[
				      { required: true, message: '批次名称不能为空', trigger: 'blur' }
				    ]">
				    <el-input v-model="form.batchName"></el-input>
				</el-form-item>
				<el-form-item label="上报开始时间" prop="reportStartDate"
				:rules="[
				      { required: true, message: '开始时间不能为空', trigger: 'change' }
				    ]">
				    <el-date-picker
				      v-model="form.reportStartDate"
				      align="right"
				      type="date"
				      placeholder="选择日期"
				      value-format="yyyy-MM-dd"
				      :picker-options="formReportStartDate">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="上报结束时间" prop="reportFinishDate"
				:rules="[
				      { required: true, message: '结束时间不能为空', trigger: 'change' }
				    ]">
				    <el-date-picker
				      v-model="form.reportFinishDate"
				      align="right"
				      type="date"
				      placeholder="选择日期"
				      value-format="yyyy-MM-dd"
				      :picker-options="formReportFinishDate">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="评审开始时间" prop="auditStartDate"
				:rules="[
				      { required: true, message: '开始时间不能为空', trigger: 'change' }
				    ]">
				    <el-date-picker
				      v-model="form.auditStartDate"
				      align="right"
				      type="date"
				      placeholder="选择日期"
				      value-format="yyyy-MM-dd"
				      :picker-options="formAuditStartDate">
				    </el-date-picker>
				</el-form-item>
				<el-form-item label="评审结束时间" prop="auditFinishDate"
				:rules="[
				      { required: true, message: '结束时间不能为空', trigger: 'change' }
				    ]">
				    <el-date-picker
				      v-model="form.auditFinishDate"
				      align="right"
				      type="date"
				      placeholder="选择日期"
				      value-format="yyyy-MM-dd"
				      :picker-options="formAuditFinishDate">
				    </el-date-picker>
				</el-form-item>
	            <el-form-item align="right">
	              <el-button type="primary" :loading="submitLoading" @click="submitForm('form')">确定</el-button>
	              <el-button @click="closeDialogForm">取消</el-button>
	            </el-form-item>
          </el-form>
		</el-dialog>
		
	</div>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex'
import itemDialog from '@/views/project/item.vue';
import SelectTree from "@/components/treeSelect.vue";
import ItemAddCom from "@/views/project/itemAddCom"
import { prjCmItem, 
getSaveTable, 
savePrjStDmd, 
getSubmitPlanLabel, 
submitPlane,
getByAuditPlanId,
changePlaneStatus,
getSpecialityIds,
getTreeDeptCode,
getUserDept,
queryFilterParam  } from '@/api/data';

import { prjStAuditBatch,prjStAuditBatchEdit,prjStAuditBatchDelete,prjStAuditBatchPublish} from '@/api/data';
import querys from '@/components/querys'

export default {
	name: 'reviewBatch',
	data() {
		var validatePass = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请输入密码'));
			} else {
				if (this.ruleForm.checkPass !== '') {
					this.$refs.ruleForm.validateField('checkPass');
				}
				callback();
			}
		};
		return {
			rules:{
				batch:[
					{ required: true, message: '请输入批次', trigger: 'blur' },
					{ pattern:/^\+?[1-9]\d*$/, message: '请输入大于0的整数', trigger: 'blur' }
				]
			},
			submitLoading:false,
			batchIds:[],
			form:{
				reportStartDate:'',
				reportFinishDate:'',
				auditStartDate:'',
				auditFinishDate:'',
				batchName:'',
				batch:'',
				speciality:'',
			},
			dialogFormVisible:false,
			dialogTitle:'',
			formReportStartDate:{
				disabledDate:this.disabledReportStartDate
			},
			formReportFinishDate:{
				disabledDate:this.disabledReportFinishDate
			},
			formAuditStartDate:{
				disabledDate:this.disabledAuditStartDate
			},
			formAuditFinishDate:{
				disabledDate:this.disabledAuditFinishDate
			},
			pickerOptions: {
	           // 限制日期
	            disabledDate: this.disabledDate
		    },
		    pickerOptionsStart:{
		       disabledDate: this.disabledDateStart
		    },
			tableData:[],
			tableShowData:[],
			tableLoading:false,
			totalNum:0,
			//查询条件
			param: {
				pageIndex: 1,
				queryType:'1',//查询2 管理1
				pageSize: 20,  
				speciality: '',  //专业类型
				reportStartDate:'',
				reportFinishDate:'',
				batchStatus:'',
				batchName:''
			},
			//查询条件
      		selectParamDict: new Map(),
			  batchIds: [],
			  delArr:[],
			  userId:"",
			  token:""
      		
		};
	},
	components: {
		querys,
		SelectTree,
		ItemAddCom
	},
	created() {
		//表格自动高度
    	this.tableHeight=window.innerHeight-220;		
	},
	mounted() {
		const _this =  this
		_this.getItemLabel()
		_this.userId = sessionStorage.getItem('userId');
    	_this.token = sessionStorage.getItem('token');
		if(_this.$route.name =="评审批次查询"){
			_this.param.batchStatus = '1'
			_this.param.queryType = '2'
			
		}else{
			_this.param.batchStatus = ''
			_this.param.queryType = '1'

			
		}
		// setTimeout(function(){_this.getTable()})
		
	},
	computed: {
		...mapGetters([
			// 'token',
		])
	},
	watch:{
		$route:{
	      handler:function(to,from){
	      	console.log(to.name)
	      	this.param={
				pageIndex: 1,
				pageSize: 20,  
				speciality: '',  //专业类型
				reportStartDate:'',
				reportFinishDate:'',
				batchStatus:'',
				queryType:'1',//查询1 管理2,
				batchName:''
			}
	      	if(to.name == '评审批次查询'){
	      		this.param.batchStatus = '1'
	      		this.param.queryType = '2'
	      		this.getTable()
	      	}else{
	      		this.param.batchStatus = ''
	      		this.param.queryType = '1'
	      		this.getTable()
	      	}
	      }
	  }
	},
	methods: {
		isSelect(row){
			if(row.batchStatus == '已发布'){
				return false
			}else{
				return true
			}
		},
		cannclSelection(){//取消选择
	      this.$refs.itemTable.clearSelection();
	    },
		publish(){
			if(this.batchIds.length){
				prjStAuditBatchPublish({'batchIds':this.batchIds.join(',')}).then(response => {
					if(response.data.successful){
							this.$message({
								showClose: true,
								message: '发布成功',
								type: 'success'
							});
							this.cannclSelection()//取消选择
							this.getTable()
						}
				})
			}else{
				this.$message({
					showClose: true,
					message: '请选择要发布的数据',
					type: 'warning'
				});
			}
		},
		// 删除
		del(){
			let _this = this
			if(_this.delArr.length){
				for(let i =0;i<_this.delArr.length;i++){
					let item = _this.delArr[i]
					if(item.createUserNoT != _this.userId){
						_this.$message({
							showClose: true,
							message: '所选项目中含有不是创建者创建的批次，请检查后再删除',
							type: 'warning'
						});
						return false
					}
				}
				_this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					
					prjStAuditBatchDelete({'batchIds':_this.batchIds.join(',')}).then(response => {
						console.log(response)
						if(response.data.successful){
							_this.$message({
								showClose: true,
								message: '删除成功',
								type: 'success'
							});
							_this.getTable()
						}
						
					})
					
				}).catch(() => {
					_this.$message({
						type: 'warning',
						message: '已取消删除'
					});
				});
			}else{
				_this.$message({
					showClose: true,
					message: '请选择要删除的数据',
					type: 'warning'
				});
				return false
			}	
		},
		handleSelectionChange(val) {
			const _this = this
			_this.delArr = []
			_this.batchIds = []
			val.forEach(function(item,idx){
				_this.delArr.push(item)
				_this.batchIds.push(item.batchId)
				
			})
		},
		submitForm(formName) {
			this.submitLoading = true
	        this.$refs[formName].validate((valid) => {
	          if (valid) {
	            
	            prjStAuditBatchEdit(this.form).then(response => {
					this.submitLoading = false
	            	console.log(response)
	            	this.closeDialogForm()
	            	if(response.data.successful){
	            		this.$message({
							showClose: true,
							message: '操作成功',
							type: 'success'
						});
	            		this.getTable()
	            	}
	            })
	          } else {
				console.log('error submit!!');
				this.submitLoading = false
	            return false;
	          }
	        });
	      },

		showEdit(title,data){
			this.dialogTitle = title
			this.dialogFormVisible = true
			
			if(data){
				let tempData = JSON.parse(JSON.stringify(data))
				this.form = tempData
				this.form.batch = tempData.batchT
				this.form.speciality = tempData.specialityT
				
			}
		},
		closeDialogForm(){
			this.dialogFormVisible = false;
			this.form = {
				reportStartDate:'',
				reportFinishDate:'',
				auditStartDate:'',
				auditFinishDate:'',
				batchName:'',
				batch:'',
				speciality:'',
			}
			this.resetForm('form')
		},
		resetForm(formName) {
	        this.$refs[formName].resetFields();
	      },
		disabledReportStartDate(time){
			const _this = this
	        return time > Date.parse(new Date(_this.form.reportFinishDate)) || time.getTime() < Date.now()- 8.64e7
		},
		disabledReportFinishDate(time){
			const _this = this
	        return time < Date.parse(new Date(_this.form.reportStartDate)) || time.getTime() < Date.now()- 8.64e7
		},
		disabledAuditStartDate(time){
			const _this = this
	        return time > Date.parse(new Date(_this.form.auditFinishDate))
		},
		disabledAuditFinishDate(time){
			const _this = this
	        return time < Date.parse(new Date(_this.form.auditStartDate))
		},
		disabledDate(time) {
	        const _this = this
	        return time < Date.parse(new Date(_this.param.reportStartDate))
	   },
	   disabledDateStart(time){
	    const _this = this
	        return time > Date.parse(new Date(_this.param.reportFinishDate))
	   },
		changeDate(str){
	     
	    },
		//获取当前表格条数
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			// this.currentNum = val;
			// this.getTable(this.currentPage, val);

			this.param.pageSize = val
			this.getTable();
		},
		//获取当前页码
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.param.pageIndex = val;
			this.getTable();
		},
		getRowKeys(row){
			return row.batchId;//返回唯一ID
		},
		//查询
		selectItem() {
			console.log(this.param)
			// this.currentPage = 1;
			this.getTable();

		},
		getTable(){
			const _this = this
			_this.tableLoading = true
			prjStAuditBatch(_this.param).then(response => {
				console.log(response)
				_this.tableLoading = false
				if(response.data.successful){
					_this.tableData = response.data.resultValue.resultList
					_this.totalNum = response.data.resultValue.total 
					// setTimeout(function () {
						console.log('selectParamDict',_this.selectParamDict)
						_this.tableShowData = _this.getShowDateTable(_this.tableData)
					// },500)
					
				}
			})
		},
		getShowDateTable(tableData){
			const _this = this
			let specArr = this.selectParamDict.get('speciality')
			tableData.forEach((item,idx) => {//翻译状态
				if(item.batchStatus == '1'){
					item.batchStatus = '已发布'
				}else{
					item.batchStatus = '未发布'
				}
				//翻译时间
				item.reportFinishDate = _this.formartDate(item.reportFinishDate)

				item.reportStartDate = _this.formartDate(item.reportStartDate)
				item.auditFinishDate = _this.formartDate(item.auditFinishDate)
				item.auditStartDate = _this.formartDate(item.auditStartDate)
				if(specArr){
					specArr.forEach((sitem,sidx) => {
						if(item.speciality == sitem.codeValue){//翻译专项
							item.speciality = sitem.codeName
							item.specialityT = sitem.codeValue
						}
					})
				}
				
			})
			console.log(tableData)
			return tableData
		},
		//获取查询条件下拉框
		getItemLabel() {
			let _this = this
			queryFilterParam().then(response => {
				console.log('response', response)//后端返回数据
				if (response.data.successful) {
					let labelList = response.data.resultValue.tableLable
					_this.selectParamDict.set('voltLevel', response.data.resultValue.voltCodeList)
					_this.selectParamDict.set('itemLevel', response.data.resultValue.itemLevelList)
					_this.selectParamDict.set('itemSource', response.data.resultValue.itemSourceList)
					_this.selectParamDict.set('speciality', response.data.resultValue.specialityList)
					_this.$forceUpdate()

					_this.getTable()
				}
			})
		},
		formartDate(param) {

			var time = new Date(param);
			var y = time.getFullYear();
			var m = time.getMonth()+1;
			var d = time.getDate();
			// var h = time.getHours();
			// var mm = time.getMinutes();
			// var s = time.getSeconds();
			// return y+'-'+this.add0(m)+'-'+this.add0(d)+' '+this.add0(h)+':'+this.add0(mm)+':'+this.add0(s);
			return y+'-'+this.add0(m)+'-'+this.add0(d);
		},
		add0(m){
			return m<10?'0'+m:m;
		},
		
	}
};
</script>
